import uPopup from '../../basic-component/u-popup/u-popup.vue';

export default {
    name: "bd-hc",
    components: {
        uPopup
    },
    props: {
        card: {
            type: Object,
            default() {
                return {};
            }
        },
        integral: {
            type: Object,
            default() {
                return {};
            }
        },
        balance: {
            type: Object,
            default() {
                return {};
            }
        },
        coupon: {
            type: Object,
            default() {
                return {};
            }
        },
        theme: {
            type: Object,
            default() {
                return {};
            }
        },
    },
    data() {
        return {
            show: false,
            showModal: '',
            title: ''
        };
    },
    methods: {
        change(key, title) {
            this.showModal = key;
            this.title = title;
            this.show = true;
        }
    }
}
